.falling-text-container {
    position: relative;
    z-index: 1;
    width: 100%;
    height: 100%;
    cursor: pointer;
    text-align: center;
    padding-top: 2em;
    /* box-shadow: none !important; */
  }

  
  .falling-text-target {
    display: inline-block;
    padding: 20px;
    max-width: 90%;
  }
  
  .word {
    display: inline-block;
    margin: 0 5px;
    padding: 5px;
    user-select: none;
    /* color: #646464; */
    /* font-weight: 100; */
    border-radius: 3px;
    /* background-color: rgba(255, 255, 255, 0.7); */
  }
  
  /* .highlighted { */
    /* color: #046bd2; Fallback */
    /* color: var(--ast-global-color-0, #046bd2); */
    /* font-weight: bold; */
  /* } */
  
  .falling-text-canvas {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
  }
  